    .accordion__button[aria-expanded="false"]::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 0;
      width: 15px;
      height: 1px;
      background-color: #ffffff;
      transform: translateY(-50%);
      transition: all 0.15s ease-in-out; }
    .accordion__button[aria-expanded="false"]::after {
